const ADD_MESSAGE = "ADD-MESSAGE";

let initialState = {
  dialogsData: [
    {
      id: 1,
      name: "Dima",
      src: "https://decider.com/wp-content/uploads/2020/12/AVA-NETFLIX-REVIEW.jpg?quality=75&strip=all",
    },
    {
      id: 2,
      name: "Lev",
      src: "data:image/jpeg;base64,",
    },
    {
      id: 3,
      name: "Marina",
      src: "data:image/jpeg;base64,",
    },
  ],
  messages: [
    {
      id: 1,
      message: "Hi",
      ava: "https://decider.com/wp-content/uploads/2020/12/AVA-NETFLIX-REVIEW.jpg?quality=75&strip=all",
    },
    {
      id: 2,
      message: "How is youe it-kamasutra?",
      ava: "data:image/jpeg;base64,",
    },
    {
      id: 3,
      message: "Yo",
      ava: "data:image/jpeg;base64,",
    },
  ],
};

const dialogsReduser = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      let body = action.newMessageBady;
      return {
        ...state,
        messages: [
          ...state.messages,
          {
            id: 4,
            message: body,
            ava: "https://decider.com/wp-content/uploads/2020/12/AVA-NETFLIX-REVIEW.jpg?quality=75&strip=all",
          },
        ],
      };
    default:
      return state;
  }
};

export const addMessageActionCreator = (newMessageBady) => ({
  type: ADD_MESSAGE,
  newMessageBady,
});

export default dialogsReduser;
